<template>
  <div>
    <div class="image-editer">
      <div class="cropper">
        <!--  -->
        <vueCropper
          class="editer"
          ref="cropper"
          :img="url"
          :info="false"
          :autoCrop="true"
          :fixed="true"
          :fixedNumber="option.aspect"
          :canMove="true"
          :centerBox="true"
          :full="true"
          @imgLoad="onImageLoad"
          @realTime="cropperChanged"
        ></vueCropper>
        <!-- ''  -->
        <div class="overlay" v-if="!haveImage" @click="getFile">
          
        </div>
        <!-- '' -->
        <div class="overlay" v-if="haveImage && loading">...</div>
      </div>
      <div class="view">
        <!--  -->
        <!-- <img ref="previewImage" /> -->
        <canvas ref="previewImage"></canvas>
      </div>
    </div>
    <div style="margin-top: 20px">
      <!--  -->
      <mButton icon="load" text="" @click="getFile" />
      <!-- input -->
      <input
        type="file"
        style="display: none"
        accept="image/*"
        @change="loadFile"
        ref="fileInput"
      />
    </div>
    <div class="setting">
      <!--  -->
      <div class="form-item">
        <div class="form-item-label">
          <span></span>
        </div>
        <div class="button-grid">
          <mButton
            :checked="option.resolution === 176"
            :url="largeLogicDisplayUrl"
            text="176"
            @click="setScreenType('large')"
          />
          <mButton
            :checked="option.resolution === 80"
            :url="logicDisplayUrl"
            text="80"
            @click="setScreenType('normal')"
          />
        </div>
      </div>
      <!--  -->
      <div class="form-item">
        <div class="form-item-label">
          <span></span>
        </div>
        <div class="button-grid">
          <mNumberCounter
            v-model="option.screenX"
            :min="1"
            :max="4"
            @change="screenSizeChange"
          />
          <mNumberCounter
            v-model="option.screenY"
            :min="1"
            :max="4"
            @change="screenSizeChange"
          />
        </div>
      </div>
      <!--  -->
      <div class="form-item">
        <div class="form-item-label">
          <span></span>
        </div>
        <div class="button-grid">
          <mButton
            v-for="(n, index) in 8"
            :key="index"
            :checked="option.compress === index"
            :text="index"
            @click="setCompress(index)"
          />
        </div>
      </div>
      <!--  -->
      <div class="form-item" v-if="option.screenX > 1 || option.screenY > 1">
        <span class="form-item-label"></span>
        <div class="button-grid">
          <mButton
            :checked="option.ignoreBorder === true"
            text=""
            @click="changeIgnoreBorder(true)"
          />
          <mButton
            :checked="option.ignoreBorder === false"
            text=""
            @click="changeIgnoreBorder(false)"
          />
        </div>
      </div>
      <!--  -->
      <div class="form-item">
        <span class="form-item-label"></span>
        <span
          ><mInput v-model="option.screenName" @input="changeScreenName" />
        </span>
      </div>
      <!--  -->
      <div class="form-item">
        <span class="form-item-label"></span>
        <span><mInput v-model="option.mschName" /> </span>
      </div>
      <!--  -->
      <div class="form-item">
        <span class="form-item-label"></span>
        <span><mInput v-model="option.mschDescription" /> </span>
      </div>
      <!--  -->
      <div class="form-item">
        <span class="form-item-label"></span>
        <div class="button-grid">
          <mButton
            :checked="option.exportType === 1"
            text=""
            @click="changeExportType(1)"
          />
          <mButton
            :checked="option.exportType === 2"
            text=""
            @click="changeExportType(2)"
          />
        </div>
      </div>
      <!--  -->
      <div class="form-item" v-if="isChanged && !loading">
        <mButton icon="play-2" text="" @click="getMlog" />
      </div>
      <div class="error-message" v-if="exportError">
        
      </div>
      <div
        class="success-message"
        v-if="exportSuccess && option.exportType == 1"
      >
        
      </div>
      <div
        class="success-message"
        v-if="exportSuccess && option.exportType == 2"
      >
        
      </div>
    </div>
    <!--  -->
    <div>
      <textarea
        ref="codeTextarea"
        class="code-textarea"
        v-model="code"
      ></textarea>
    </div>
    <!--  -->
    <div class="code-list">
      <div class="code-item" v-for="(chips, index) in screenMlog" :key="index">
        <div class="code-title">
          <div style="width: 80px">{{ index }}</div>
          <!-- <div>
            ( {{ index % option.screenX }} ,
            {{ Math.floor(index / option.screenX) }} )
          </div> -->
        </div>
        <div class="code-botton-list">
          <mButton
            v-for="(chip, index2) in chips"
            :key="index2"
            :text="index2"
            :error="chip.copyed"
            @click="copyCode(chip)"
          />
        </div>
      </div>
    </div>
    <div class="mask" :class="{ show: exporting }">
      <div class="exporting-state">
        {{ exportingState }}
      </div>
    </div>
  </div>
</template>

<script>
//
import { VueCropper } from 'vue-cropper'
//
import mButton from '@/components/ui/button'
//
import mInput from '@/components/ui/input'
//
import mNumberCounter from '@/components/ui/numberCounter'
//
import PreviewCnavas from './previewCnavas.js'
// import CodeToMsch from './CodeToMsch'
import WorkerController from './WorkerController.js'
export default {
  //
  components: {
    mButton,
    mInput,
    mNumberCounter,
    VueCropper,
  },
  data() {
    return {
      //
      url: null,
      //
      loading: true,
      //
      haveImage: false,
      //
      option: {
        //
        resolution: 176,
        //
        compress: 5,
        //
        screenName: 'display1',
        //x
        screenX: 1,
        //y
        screenY: 1,
        //
        aspect: [1, 1],
        //
        ignoreBorder: true,
        //
        mschName: 'pic',
        //
        mschDescription: '',
        //
        exportType: 1,
      },
      //
      previewCnavas: null,
      outImage: null,
      //
      previewTimer: null,
      //
      code: '',
      //
      screenMlog: [],
      //
      isChanged: false,
      largeLogicDisplayUrl: require('@/assets/images/blocks/logic/large-logic-display.png'),
      logicDisplayUrl: require('@/assets/images/blocks/logic/logic-display.png'),
      exportError: false,
      exportSuccess: false,
      exporting: false,
      exportingState: '',
    }
  },
  methods: {
    //
    loadFile(e) {
      if (e.target.files.length != 0) {
        this.haveImage = true
        this.loading = true
        var reader = new FileReader()
        reader.readAsDataURL(e.target.files[0])
        reader.onload = e => {
          //
          this.url = e.target.result
        }
      }
    },
    //
    onImageLoad(e) {
      if (e === 'success') {
        this.loading = false
        this.imageChanged()
      }
    },
    //
    screenSizeChange() {
      this.previewCnavas.setScreenCount(
        this.option.screenX,
        this.option.screenY,
      )
      this.option.aspect = this.previewCnavas.getAspect()
      this.$nextTick(() => {
        //
        this.$refs.cropper.goAutoCrop()
      })
      this.imageChanged()
    },
    //input
    getFile() {
      this.$refs.fileInput.click()
    },
    //
    setScreenType(type) {
      switch (type) {
        case 'normal':
          this.option.resolution = 80
          this.previewCnavas.setScreenType('normal')
          break
        case 'large':
          this.option.resolution = 176
          this.previewCnavas.setScreenType('large')
          break
      }
      this.imageChanged()
    },
    //
    setCompress(n) {
      this.option.compress = n
      this.previewCnavas.setCompress(n)
      this.imageChanged()
    },
    //
    cropperChanged() {
      if (this.previewTimer) {
        clearTimeout(this.previewTimer)
        this.previewTimer = null
      }
      if (this.haveImage && !this.loading) {
        this.previewTimer = setTimeout(() => {
          this.$refs.cropper.getCropData(data => {
            this.previewCnavas.setImage(data)
          })
          this.imageChanged()
        }, 100)
      }
    },
    //
    changeIgnoreBorder(ignoreBorder) {
      this.option.ignoreBorder = ignoreBorder
      this.previewCnavas.setSreenIgnoreBorder(ignoreBorder)
      this.$nextTick(() => {
        this.screenSizeChange()
      })
      this.imageChanged()
    },
    //
    getMlog() {
      const imageDatas = this.previewCnavas.getImageData()
      const workerController = new WorkerController(
        this.onStart,
        this.onPercentage,
        this.onEnd,
        this.onError,
      )
      workerController.run({
        imageDatas,
        screenName: this.option.screenName,
        screenType: this.previewCnavas.screenOption.screenData.type,
        mschName: this.option.mschName,
        mschDescription: this.option.mschDescription,
        screenX: this.option.screenX,
        screenY: this.option.screenY,
        exportType: this.option.exportType,
      })
      this.exportingState = ''
      this.exportError = false
      this.exportSuccess = false
      this.exporting = true
      this.isChanged = false
    },
    //
    changeScreenName() {
      this.previewCnavas.setScreenName(this.option.screenName)
      this.imageChanged()
    },
    //
    copyCode(chip) {
      console.log(chip)
      this.code = chip.code
      this.$set(chip, 'copyed', true)
      this.$nextTick(() => {
        this.$refs.codeTextarea.select()
        document.execCommand('copy')
      })
    },
    imageChanged() {
      this.isChanged = true
      this.exportError = false
      this.exportSuccess = false
    },
    changeExportType(type) {
      this.option.exportType = type
      this.imageChanged()
    },
    onStart() {
      this.exportingState = ''
    },
    onPercentage(message) {
      this.exportingState = message
    },
    onEnd(data) {
      this.exporting = false
      this.exportSuccess = true
      if (this.option.exportType == 1) {
        this.code = data
        this.$nextTick(() => {
          this.$refs.codeTextarea.select()
          document.execCommand('copy')
          this.code = ''
        })
      } else {
        const blob = new Blob([data])
        // 
        let eleLink = document.createElement('a')
        eleLink.download = `${this.option.mschName}.msch`
        eleLink.style.display = 'none'
        // blob
        eleLink.href = URL.createObjectURL(blob)
        // 
        document.body.appendChild(eleLink)
        eleLink.click()
        // 
        document.body.removeChild(eleLink)
      }
      this.exportSuccess = true
    },
    onError() {
      this.exporting = false
      this.exportError = true
    },
  },
  //
  mounted() {
    //
    this.previewCnavas = new PreviewCnavas(this.$refs.previewImage)
    //
    this.changeScreenName()
  },
  //
  destroyed() {
    if (this.previewTimer) {
      clearTimeout(this.previewTimer)
    }
  },
}
</script>

<style lang="scss" scoped>
.image-editer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .cropper {
    flex: 1;
    min-width: 300px;
    height: 400px;
    position: relative;
    .editer {
      width: 100%;
      height: 100%;
    }
    .overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background-color: rgba(0, 0, 0, 0.5);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      user-select: none;
      cursor: pointer;
    }
  }
  .view {
    flex: 1;
    min-width: 300px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: black;
    canvas {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      object-fit: contain;
      object-position: center center;
    }
  }
}

.setting {
  margin-top: 20px;
}

.form-item {
  display: flex;
  flex-direction: row;
  min-height: 54px;
  & + & {
    margin-top: 10px;
  }
  .form-item-label {
    width: 90px;
    height: 54px;
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }
}
.code-textarea {
  width: 0px;
  height: 0px;
  background-color: transparent;
  position: absolute;
  left: -10px;
  top: 0;
  border: none;
  resize: none;
}
// @media screen and (max-width: 800px) {
//   .image-editer {
//     flex-direction: column;
//   }
//   .cropper {
//     flex: unset;
//     height: 400px;
//   }
// }
.code-list {
  .code-item {
    margin: 30px 0;
    .code-title {
      color: rgb(255, 211, 127);
      border-bottom: 4px rgb(255, 211, 127) solid;
      margin-bottom: 8px;
      display: flex;
    }
    .code-botton-list {
      .m-button {
        width: 65px;
        margin: 5px;
      }
    }
    &:last-child {
      margin-bottom: 100px;
    }
  }
}
.error-message {
  margin-top: 30px;
  color: rgb(229, 84, 84);
}
.success-message {
  margin-top: 30px;
  color: rgb(255, 211, 127);
}
.button-grid {
  display: flex;
  flex-wrap: wrap;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}
.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: 0.3s linear;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mask.show {
  opacity: 1;
}
.exporting-state {
  color: rgb(255, 211, 127);
}
</style>
